<template>
  <div class="article-manager">
    <div class="main">
      <div class="content">
        <el-table :data="article" style="width: 100%" :row-class-name="tableRowClassName" border>
          <el-table-column prop="id" label="文章id" width="100">
          </el-table-column>
          <el-table-column prop="update_date" label="日期" width="180">
          </el-table-column>
          <el-table-column prop="author" label="作者" width="180">
          </el-table-column>
          <el-table-column prop="article_title" label="标题" width="300">
            <!-- <template slot-scope="scope">
              <span class="article-title" :title="scope.row.title">{{ scope.row.title }}</span>
            </template> -->
          </el-table-column>

          <el-table-column label="操作" fixed="right" width="180">
            <template slot-scope="scope">
              <el-button @click="handleClick(scope.row)" type="primary" size="mini">查看</el-button>

              <el-button type="danger" size="mini" @click="deleteArticleById(scope.row.id)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="d-fourBlock"></div>
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :page-size="5" layout="prev, pager, next, jumper"
          :total="article_count" :current-page="currentPage">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'ArticleManager',
    data() {
      return {
        url: '/article',
        article: [],
        article_count: 0, // 文章总数
        currentPage: 1 // 当前页码
      }
    },
    created() {
      this.getArticleCount();
      this.getLimitArticle(0);
    },
    methods: {
      tableRowClassName({
        row,
        rowIndex
      }) {
        if (rowIndex === 1) {
          return 'warning-row';
        } else if (rowIndex === 3) {
          return 'success-row';
        }
        return '';
      },
      handleClick(row) {
        console.log(row);
      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        this.getLimitArticle((val - 1) * 5);
        // this.currentPage = val;
      },
      // 获取数据
      getLimitArticle: function(index) {
        this.article = [];
        this.loading = true;
        this.$axios.get(this.url + '/limit' + `/${index}`).then(res => {
          if (res.data.code == 200) {
            this.article = res.data.body;
            this.loading = false;
          } else {
            this.$message({
              message: res.data.message,
              type: 'warning'
            })
          }
        })
      },
      // 获取文章数
      getArticleCount: function() {
        this.$axios.get(this.url + '/articleCount').then(res => {
          if (res.data.code == 200) {
            this.article_count = res.data.body;
          } else {
            this.$message({
              message: res.data.message,
              type: 'warning'
            })
          }
        })
      },
      deleteArticleById: function(id) {
        this.$axios.get(this.url + '/deleteArticle/' + id).then(res => {
          if (res.data.code == 200) {
            this.$message({
              message: res.data.message,
              type: 'success'
            });
            this.handleCurrentChange(this.currentPage);
            this.getArticleCount();
          } else {
            this.$message({
              message: res.data.message,
              type: 'warning'
            });
          }
        })
      }
    }
  }
</script>

<style scoped>
  .el-table .warning-row {
    background: oldlace;
  }

  .el-table .success-row {
    background: #f0f9eb;
  }

  .article-title {
    display: block;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
</style>



